const AdminPerks = () => {
  const [perks, setPerks] = useState([]);
  const [newPerk, setNewPerk] = useState('');
  const [newPerkIcon, setNewPerkIcon] = useState('');

  const API_BASE_URL = import.meta.env.VITE_BASE_URL;

  useEffect(() => {
    const token = localStorage.getItem('token');

    axios
      .get(`${API_BASE_URL}/api/admin/perks`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((response) => {
        const fetchedPerks = response.data.data;
        const perksWithIcons = fetchedPerks.map((perk) => {
          const foundPerk = basePerks.find((p) => p.name === perk.name);
          return foundPerk ? { ...perk, icon: foundPerk.icon } : perk;
        });
        setPerks(perksWithIcons);
      })
      .catch((error) => console.error(error));
  }, [API_BASE_URL]);

  const handleAddPerk = () => {
    const token = localStorage.getItem('token');
    if (newPerk.trim() === '' || newPerkIcon.trim() === '') return;

    axios
      .post(
        `${API_BASE_URL}/api/admin/perks`,
        { name: newPerk, icon: newPerkIcon },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        },
      )
      .then((response) => {
        const addedPerk = { name: newPerk, icon: newPerkIcon };
        setPerks([...perks, addedPerk]);
        setNewPerk('');
        setNewPerkIcon('');
      })
      .catch((error) => console.error(error));
  };

  const handleDeletePerk = (perkName) => {
    const token = localStorage.getItem('token');

    if (window.confirm('Êtes-vous sûr de vouloir supprimer ce perk ?')) {
      axios
        .delete(`${API_BASE_URL}/api/admin/perks/${perkName}`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then((response) => {
          setPerks(perks.filter((perk) => perk.name !== perkName));
        })
        .catch((error) => console.error(error));
    }
  };

  return (
    <div className="container mx-auto mt-8 p-4">
      <h2 className="text-2xl font-bold mb-4">Gestion des Perks</h2>

      <div className="mb-4">
        <input
          type="text"
          placeholder="Nom du perk..."
          value={newPerk}
          onChange={(e) => setNewPerk(e.target.value)}
          className="p-2 border rounded mr-2"
        />
        <input
          type="text"
          placeholder="URL de l'icone..."
          value={newPerkIcon}
          onChange={(e) => setNewPerkIcon(e.target.value)}
          className="p-2 border rounded mr-2"
        />
        <button
          onClick={handleAddPerk}
          className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-700"
        >
          Ajouter
        </button>
      </div>

      <table className="min-w-full bg-white shadow-md rounded-lg overflow-hidden">
        <thead>
          <tr>
            <th className="px-4 py-2 text-center">Perk</th>
            <th className="px-4 py-2 text-center">Actions</th>
          </tr>
        </thead>
        <tbody>
          {Array.isArray(perks) && perks.length > 0 ? (
            perks.map((perk, index) => (
              <tr key={index} className="border-t">
                <td className="px-4 py-2 text-center">
                  <div className="flex items-center justify-center">
                    <div className="mr-2">
                      {perk.icon ? (
                        perk.icon
                      ) : (
                        <img src={perk.iconUrl} alt={perk.name} />
                      )}
                    </div>
                    <span>{perk.name}</span>
                  </div>
                </td>
                <td className="px-4 py-2 text-center">
                  <button
                    className="bg-red-500 text-white px-2 py-1 rounded hover:bg-red-700"
                    onClick={() => handleDeletePerk(perk.name)}
                  >
                    Supprimer
                  </button>
                </td>
              </tr>
            ))
          ) : (
            <tr>
              <td colSpan="2" className="px-4 py-2 text-center">
                Aucun perk trouvé.
              </td>
            </tr>
          )}
        </tbody>
      </table>
    </div>
  );
};

export default AdminPerks;